    };
  },
  methods: {
    pass(num) {
      this.range = num;
    },
    // get which tab the user clicked
    handleClick(tab, event) {
      console.log(event.target.innerText);
      switch (event.target.innerText) {
        case "70-74":
          this.pass(0);
