import * as echarts from 'echarts'
import { getpop } from '@/network/api'

// draw the map of austrilia
export const drawmap = async (dom, v, num) => {
  let myChart = echarts.init(dom)
  let option
  // let statemap = {
  //   'New South Wales': 'NSW',
  //   'Victoria': 'VIC',
  //   'Queensland': "QLD",
  //   'South Australia': 'SA',
  //   'Western Australia': 'WA',
  //   'Tasmania': 'TAS',
  //   'Northern Territory': 'NT',
  //   'Australian Capital Territory': 'ACT'
  // }

  myChart.showLoading();
  //get the case and deaths data
  let result = await getpop();
  result = result[num]
  myChart.hideLoading();

  echarts.registerMap('AUS', v)

  // set option of the map

  option = {
    title: {
      text: 'Population by Age Group',
      subtext: `Age Group: ${result.A}`,
      left: 'right'
    },
    tooltip: {
      trigger: 'item',
      showDelay: 0,
      transitionDuration: 0.2,
      formatter: function (params) {
        var value = (params.value + '').split('.');
        value = value[0].replace(/(\d{1,3})(?=(?:\d{3})+(?!\d))/g, '$1,');
        // return params.seriesName + '<br/>' + params.name + ': ' + value;
        // hover text showup
        // return params.name + '<br/>' + 'Cases: ' + value + '<br/>' + 'Deaths: ' + result[`${statemap[params.name]}_Deaths`];
        return params.name + '<br/>' + 'Population: ' + value
      }
    },
    visualMap: {
      left: 'right',
      min: 3000,
      max: 400000,
      inRange: {
        // color: ['lightskyblue', 'yellow', 'orangered']
        color: ['#313695', '#4575b4', '#74add1', '#abd9e9', '#e0f3f8', '#ffffbf', '#fee090', '#fdae61', '#f46d43', '#d73027', '#a50026']
      },
      text: ['High', 'Low'],      
      calculable: true
    },
    toolbox: {
      show: true,
      //orient: 'vertical',
      left: 'left',
      top: 'top',
      // feature: {
      //   dataView: { readOnly: false },
      //   restore: {},
      //   saveAsImage: {}
      // }
    },
    series: [
      {
        name: 'AUS',
        type: 'map',
        roam: true,
        map: 'AUS',
        label: {
          show: true
        },
        emphasis: {
          label: {
            show: true
          }
        },
        // textFixed: {
        //   Alaska: [20, -20]
        // },
        data: [
          { name: 'New South Wales', value: result.B },
          { name: 'Victoria', value: result.C },
          { name: 'Queensland', value: result.D },
          { name: 'South Australia', value: result.F },
          { name: 'Western Australia', value: result.E },
          { name: 'Tasmania', value: result.G },
          { name: 'Northern Territory', value: result.I },
          { name: 'Australian Capital Territory', value: result.H },
        ]
      }
    ]
  };

  myChart.setOption(option);

  option && myChart.setOption(option);
}