<template>
  <div ref="apd" class="main"></div>
</template>

<script>
import { drawmap } from "@/utils/map.js";
import { ausmap } from "@/network/api";

export default {
  data() {
    return {
      // range: 0
    };
  },
  props:['range'],
  async created() {
    let result = await ausmap();
    // pass the dom element, data (from api), and age group
    this.redraw(this.$refs.apd, result, this.range);
  },
  methods: {
    redraw(dom, v, num) {
      drawmap(dom, v, num)
    }
  },
  mounted() {},
  watch: {
    // listen the range data, when it changes, then executes the following functions.
    async range(newnum, oldnum) {
      let result = await ausmap()
      if(newnum !== oldnum) {
        this.redraw(this.$refs.apd, result, newnum);
      }
    }
  }
};
</script>
<style scoped>
.main {
  width: 600px;
  height: 400px;
}
</style>